.footer {
	margin-top: auto;
	background: black;
	opacity: 0.75;
	color: white;
	display: flex;
	flex-direction: row;
	align-items: flex-start;
	justify-content: center;
	white-space: nowrap;
	overflow: hidden;
}

.footer-column-wrapper {
	display: flex;
	align-items: flex-start;
	justify-content: center;
	flex-direction: row;
}

.footer-column {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: 1.5rem 2rem 2rem 2rem;
	margin-bottom: calc(6vh - 2rem);
}

.footer-content {
	text-decoration: none;
	color: #fff;
	margin-bottom: 0.5rem;
	font-size: 1.2rem;
}

.footer-content:hover {
	opacity: 0.5;
	transition: all 0.2s ease-out;
}

.footer-content > h1 {
	font-size: 2rem;
	margin-bottom: 0.5rem;
}

.footer-row {
	font-size: 2.1rem;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	padding: 0 0 0 3%;
	align-self: center;
}

.brand-container {
	margin: 0 1rem;
}

.fa-brands {
	color: white;
}

.fa-brands:hover {
	opacity: 0.5;
	transition: all 0.2s ease-out;
}

@media screen and (max-width: 960px) {
	.footer {
		flex-direction: column;
		align-items: center;
	}

	.footer-row {
		padding: 0.5rem 0 2rem 0;
	}
}

@media screen and (max-width: 600px) {
	.footer-column-wrapper {
		flex-direction: column;
		align-items: center;
	}
}