.about-wrapper {
	padding: 3rem;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-start;
	background: rgba(250, 250, 250, 0.96);
	min-height: calc(100vh - 80px);
}

.about-wrapper > h1 {
	padding-bottom: 2.5rem;
}

.timeline-bar {
	display: grid;
	justify-content: flex-start;
	align-items: flex-start;
	overflow-x: scroll;
	scroll-snap-type: x mandatory;
	width: 100%;
	padding-bottom: 1rem;
	margin-bottom: 2rem;
}

/* .timeline-bar::-webkit-scrollbar {
	display: none;
} */

.body-grid {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	justify-content: center;
	gap: 1rem;
	width: 100%;
}

.left-column {
	display: flex;
	flex-direction: column;
	align-items: center;
	/* justify-content: center; */
	padding: 0.5rem;
	border: 1px solid black;
}

.left-column > h2 {
	padding-bottom: 1.5rem;
}

.middle-column {
	padding: 0.5rem;
	display: flex;
	flex-direction: column;
	align-items: center;
	/* justify-content: center; */
	border: 1px solid black;
}

.right-column {
	padding: 0.5rem;
	display: flex;
	flex-direction: column;
	align-items: center;
	/* justify-content: center; */
	border: 1px solid black;
}
